import Ding from 'dings'; 

async function init(){
    const isAuth = await Ding.ddConfig();  /* api */
    console.log( isAuth ); 
}

const { DINTALK_API } = require(`config/${ENV}.json`);
if ( DINTALK_API == true ) { 
    init()
} else {
    alert ( ' Dingtalk jsApi :  () ')
}






