import { use, LogicRender } from 'refast';

import { Toast } from "antd-mobile";
import Icons from 'assets/icon';
import axios from 'axios'
/*  */
import Api from './api';
import _ from 'lodash'
/*  */
const Loading = () => { Toast.loading('...', 0, ()=>{console.log('!!!')}) };
const Empty = () => { Toast.fail('!!!', 1) };
const attachComponents = { Loading, Empty };

/* logic */
use('Api', Api);
use('http', axios)
use('Loading', Loading);
use('Icon', Icons );

/*LogicRender*/
LogicRender.defaultProps = _.assign( LogicRender.defaultProps, attachComponents );



